import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl, Validators } from "@angular/forms";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { SpinnerService } from "src/app/shared/spinner.service";
import { UtilsService } from "src/app/shared/utils.service";
import { TaDataStoreService } from "../../ta-datastore.service";
import { TextAnalysisService } from "../../text-analysis.service";

@Component({
  selector: "app-text-summarization",
  templateUrl: "./text-summarization.component.html",
  styleUrls: ["./text-summarization.component.scss"],
})
export class TextSummarizationComponent implements OnInit {
  @Input() operation: any;

  @Output() onRemove = new EventEmitter();

  /**
   * models array
   */
  modelsList: Array<any> = [
    {
      modelName: "Scoring Model",
      modelKey: "scoringmodel",
    },
    {
      modelName: "Similarity Model",
      modelKey: "similaritymodel",
    },
    {
      modelName: "Abstractive model-T5",
      modelKey: "t5model",
    },
  ];

  informationRetained: Number = 50;

  binColor: string = "basic";

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  showTextError: Boolean = false;
  showError: Boolean = false;
  showOutput: Boolean = false;

  output: any;

  copyColor: string = "basic";

  tooltipText: string = "Click to copy!";

  inputData: any;
  errorMessage: string;
  textSummarizationModelControl = new FormControl("", Validators.required);
  minSequence = new FormControl("", [
    Validators.required,
    Validators.pattern("^[0-9]*$"),
  ]);
  maxSequence = new FormControl("", [
    Validators.required,
    Validators.pattern("^[0-9]*$"),
  ]);
  constructor(
    private _taDataStore: TaDataStoreService,
    private _textAnalysis: TextAnalysisService,
    private _spinner: SpinnerService,
    public _utils: UtilsService
  ) { }

  ngOnInit(): void {
    this.inputData = this._taDataStore.getInputData(this.operation);
    this.setData();
    this._taDataStore.onSubmitEventSubject.subscribe((val) => {
      this.inputData = this._taDataStore.getInputData(this.operation);
    });
  }

  //function to set db data to component
  setData(): void {
    let loadData = this._taDataStore.getAllOperationData();
    loadData.forEach((loadVal) => {
      if (loadVal.uid == this.operation.uid) {
        if (loadVal.taskConfig.operationParameters) {
          this.textSummarizationModelControl.setValue(
            loadVal.taskConfig.operationParameters.model
          );
          this.minSequence.setValue(
            loadVal.taskConfig.operationParameters.t5minlength
          );
          this.maxSequence.setValue(
            loadVal.taskConfig.operationParameters.t5maxlength
          );
          this.informationRetained =
            loadVal.taskConfig.operationParameters.informationRetained;
        }
      }
    });
  }

  /**
   * function to pass the value to thumb of the slider.
   * @param value
   * @returns
   */
  informationRetainedSliderLabel(value: Number) {
    return value;
  }

  mouseOverBin(): void {
    this.binColor = "warn";
  }

  mouseLeaveBin(): void {
    this.binColor = "basic";
  }

  /**
   * function to remove operation
   */
  removeOperation(): void {
    //Remove the operation data from data store
    this._taDataStore.removeOperation(this.operation);
    //Emit the remove event ot parent
    this.onRemove.emit(this.operation);
  }

  mouseOverCopy(): void {
    this.copyColor = "primary";
  }

  mouseLeaveCopy(): void {
    this.copyColor = "basic";
  }

  //function to copy summarization output
  copyOutput = function () {
    let textToCopyElement = document.getElementById(
      "summarizationOutput"
    ).innerText;
    let dummyElem = document.createElement("textarea");
    document.body.appendChild(dummyElem);
    dummyElem.value = textToCopyElement;
    dummyElem.select();
    document.execCommand("copy");
    document.body.removeChild(dummyElem);
    this.tooltipText = "Copied!";
  };

  onSubmit = function (
    modelData: FormControl,
    sliderVal: Number,
    minSequence: FormControl,
    maxSequence: FormControl
  ) {
    this._spinner.showSpinner();
    this.showTextError = false;
    let inputData = this._taDataStore.getInputData(this.operation);
    inputData = inputData?.output;
    if (!inputData) {
      this.showTextError = true;
      this._spinner.stopSpinner();
      return;
    }
    if (modelData.value == "t5model") {
      if (minSequence.value == null || maxSequence.value == null) {
        this.showError = true;
        this._spinner.stopSpinner();
        this.errorMessage = "Enter the Minimum and maximum sequence value";
        return;
      } else {
        this.showError = false;
      }
      if (maxSequence.value == 0) {
        this.showError = true;
        this._spinner.stopSpinner();
        this.errorMessage =
          "Maximum length of sequence  number should be greater than zero.";
        return;
      }
    }
    //object to send to the summarization api.
    let summarizeParam = {
      data: inputData,
      taskConfig: [
        {
          operationName: "textSummarization",
          operationParameters: {
            model: modelData.value,
            informationRetained: sliderVal,
            t5minlength: this.minSequence.value,
            t5maxlength: this.maxSequence.value,
          },
        },
      ],
    };
    if (modelData.value == "t5model") {
      delete summarizeParam.taskConfig[0].operationParameters[
        "informationRetained"
      ];
    }
    this._textAnalysis
      .textSummarization(summarizeParam)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this.showOutput = true;
          this.output = res.data.data;
          //save result back to the service
          this._taDataStore.saveOperationOutput({
            uid: this.operation.uid,
            operation: "textSummarization",
            output: this.output,
            taskConfig: summarizeParam.taskConfig,
          });
          // vm.showLoading = false;
          this._spinner.stopSpinner();
          // $rootScope.$broadcast("onSubmit");
          this._taDataStore.broadcastSubmitEvent("updateInputBar");
        },
        (err: Error) => {
          this.showOutput = false;
          // toaster.pop('error', $scope.app.name, 'Error Summarizing text', 3000);
          // vm.showLoading = false;
          this._spinner.stopSpinner();
          this._toaster.error("Server Error", "Error performing text summarization");
        }
      );
  };

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
