import {
  Component,
  ElementRef,
  Inject,
  OnInit,
  ViewChild,
} from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { FormArray, FormControl, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { ToastrService } from "ngx-toastr";
import { LocalStorageService } from "src/app/shared/local-storage.service";
import { SpinnerService } from "src/app/shared/spinner.service";
import { NerComponent } from "../operations-ui-container/ner/ner.component";
import { TaDataStoreService } from "../ta-datastore.service";
import { TextAnalysisService } from "../text-analysis.service";

export interface DialogData {
  list: any;
  selectedVal: any;
}

@Component({
  selector: "app-pipeline-settings-dialog",
  templateUrl: "./pipeline-settings-dialog.component.html",
  styleUrls: ["./pipeline-settings-dialog.component.scss"],
})
export class PipelineSettingsDialogComponent implements OnInit {
  project: any;
  user: any;
  form: FormGroup;

  operationsToolBar: ElementRef;

  pipelineSettingsControl = new FormControl("");
  featureArray: FormArray;
  valueFromUser: any = [];

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _taDataStore: TaDataStoreService,
    private _textAnalysis: TextAnalysisService,
    private _toaster: ToastrService,
    private _pipelineSettingsDialog: MatDialogRef<NerComponent>,
    private fb: FormBuilder
  ) {
    this.project = this._localStorage.getItem("project");
    this.user = this._localStorage.getItem("user");
    this.form = this.fb.group({
      featureArray: this.fb.array([]),
    });
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.featureArray = this.form.get("featureArray") as FormArray;
    this.data.selectedVal &&
      this.data.selectedVal.forEach((item) => {
        this.featureArray.push(new FormControl(item));
        this.valueFromUser && this.valueFromUser.push(item);
      });
    this._spinner.stopSpinner();
  }

  setPipelineSettings(checkboxData: FormGroup): void {
    this._pipelineSettingsDialog.close(checkboxData.value);
  }

  onCheckBoxValChange(e): void {
    if (e.source._checked) {
      this.featureArray.push(new FormControl(e.source.value));
    } else {
      let i: number = 0;
      this.featureArray.controls.forEach((item: FormControl) => {
        if (item.value == e.source.value) {
          this.featureArray.removeAt(i);
          return;
        }
        i++;
      });
    }
  }
}
