import { takeUntil } from 'rxjs/operators';
import { SpinnerService } from './../../../shared/spinner.service';
import { LocalStorageService } from './../../../shared/local-storage.service';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { DataService } from './../../data/data.service';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TextAnalysisService } from '../text-analysis.service';
import { TaDataStoreService } from '../ta-datastore.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-experiment-input',
  templateUrl: './experiment-input.component.html',
  styleUrls: ['./experiment-input.component.scss'],
})
export class ExperimentInputComponent implements OnInit {
  @Input() operationList: any;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  showSpinner: boolean = false;
  isBlur: boolean = true;
  user: any;
  project: any;
  selectedDataSet: any;
  selectedDataSetVal: any;
  childFileData: any;
  edaData: any;
  filename: string = '';
  showError: boolean = false;
  selectedFeature: string;
  featureDescription: string = '';
  dataSetArray: Array<any>;
  featureArray: Array<any>;
  selectedOperations: Array<any>;
  experimentsList: Array<any>;

  constructor(
    private _data: DataService,
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _textAnalysis: TextAnalysisService,
    private _taDataStore: TaDataStoreService,
    private route: ActivatedRoute
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getDataSetList();
  }

  /**
   * function to get all the dataset uploaded for the specific project
   */
  getDataSetList(): void {
    this._data
      .getAllProjectDatas(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this._spinner.stopSpinner();
          if (res) {
            this.dataSetArray = res[0].filesList.filter((item) => {
              return item.fileSource !== 'upload_retrained';
            });
            this.setValuesAfterRefresh();
          } else {
            this._toaster.error('Cannot fetch all data set', 'Text Analysis');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Text Analysis');
        }
      );
  }

  //function to load all value again after refresh of page
  setValuesAfterRefresh(): void {
    let experimentState = this._localStorage.getItem('experimentMode');
    this._textAnalysis
      .experimentList(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this.experimentsList = res;
          this.experimentsList.forEach((experiment) => {
            if (experiment._id == this.route.snapshot.queryParams.experimentId) {
              // this.getDataSetList();
              this.setExperimentsData(experiment);
              // this.loadExperimentsData();
            }
          });
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Text Analysis');
        }
      );
  }

  //function to call service method to store the model data for exeriments
  setExperimentsData = function (operationsData: any) {
    if (operationsData.fileId == null) {
      this.featureDescription = operationsData.inputText;
    }
    // if (mode) {
    //   this._localStorage.setItem("experimentMode", mode);
    // } else {
    //   this._localStorage.removeItem("experimentMode");
    // }
    this.onChangeDataset(operationsData.fileId, true, operationsData.feature);
    // let inputDataForOperations: any = {
    //   uid: 1,
    //   featureName: operationsData.feature,
    //   fileId: operationsData.fileId,
    //   operation: "inputData",
    //   output: operationsData.inputText,
    //   experimentName: operationsData.experimentName,
    // };

    if (this.selectedDataSetVal) {
      let taData = this._taDataStore.getAllOperationData(true);
      taData.forEach((data) => {
        if (data.operation == 'inputData') {
          data.fileName = this.selectedDataSetVal.name;
          data.dataSetVal = this.selectedDataSetVal;
          this.selectedDataSet = data.fileId;
          // this.onChangeDataset(this.selectedDataSet, true, data.featureName);
          this.featureDescription = data.output;
          // vm.experimentName = data.experimentName;
          this.filename = this.selectedDataSetVal.name;
        }
      });
    }
    // this._taDataStore.setExperimentsData(inputDataForOperations);
    // operationsData.taskConfig.forEach((operation) => {
    //   let dataToSave = {
    //     uid: operation.uid,
    //     operation: operation.operationName,
    //     taskConfig: operation.parameters,
    //   };
    //   this._taDataStore.setExperimentsData(dataToSave);
    // });
  };

  //function to load experiments data
  // loadExperimentsData = () => {
  //   let taData = this._taDataStore.getAllOperationData(true);
  //   if (taData.length) {
  //     taData.forEach((data) => {
  //       this.operationList.forEach((operation) => {
  //         if (data.operation == "inputData") {
  //         }
  //       });
  //     });
  //   }
  // };

  /**
   * function called each time the user chooses a different dataset
   * @param event
   */
  onChangeDataset(dataSet: any, callFromRefreshLogic?: any, featureName?: any): void {
    this._spinner.showSpinner();
    this.selectedDataSetVal = this.dataSetArray.find((data: any) => data._id == dataSet);
    if (dataSet !== null) {
      this._data
        .getAllChildDatasets(this.project._id, dataSet)
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(
          (res: any) => {
            if (res) {
              if (res.length > 0 && res[0].currentEdaId) {
                this.childFileData = res[0];
                this.edaData = res[0].currentEdaId;
                this.featureArray = this.edaData.edaSummary.filter(
                  (data: any) => data.dataType == 'Text'
                );
              } else {
                this.edaData = this.selectedDataSetVal.fileSchema;
                this.featureArray = this.edaData.filter((data: any) => data.dataType == 'Text');
              }
              if (!this.featureArray.length) {
                this.showError = true;
              } else {
                this.showError = false;
              }
            } else {
              this._toaster.error('Cannot fetch all data set', 'Text Analysis');
            }
            if (callFromRefreshLogic) {
              this.selectedFeature = featureName;
              // vm.feature = featureData;
            }
            this._spinner.stopSpinner();
          },
          (err: Error) => {
            this._spinner.stopSpinner();
            this._toaster.error('Server Error', 'Text Analysis');
          }
        );
    }
  }

  /**
   * when user edits the text box
   * @param {*} data
   */
  onTextChange = function (data: string) {
    if (this.childFileData && this.childFileData.fileSource == 'eda_') {
      this.filename = this.childFileData.filename;
    } else {
      this.filename = this.selectedDataSetVal.name;
    }
    this._taDataStore.saveOperationOutput({
      uid: 1,
      operation: 'inputData',
      output: data,
      featureName: this.selectedFeature,
      fileName: this.filename,
      fileId: this.selectedDataSetVal._id,
      dataSetVal: this.selectedDataSetVal,
      childFileData: this.childFileData,
    });
  };

  /**
   * function to compute after feature selected is changed
   * @param {*} feature
   */
  onChangeFeature = (feature: any) => {
    this.filename = '';
    // this.showData = true;
    if (this.childFileData && this.childFileData.fileSource == 'eda_') {
      this.filename = this.childFileData.filename;
    } else {
      this.filename = this.selectedDataSetVal.name;
    }
    this.selectedFeature = feature;
    this.refresh();
  };

  refresh = () => {
    if (this.selectedFeature && this.selectedFeature.length > 0) {
      this._spinner.showSpinner();
      this._textAnalysis
        .listFeatureDetails(this.project._id, this.selectedFeature, this.filename)
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(
          (res: any) => {
            this.featureDescription = res.data;
            this._taDataStore.saveOperationOutput({
              uid: 1,
              operation: 'inputData',
              output: this.featureDescription,
              featureName: this.selectedFeature,
              fileName: this.filename,
              fileId: this.selectedDataSetVal._id,
              dataSetVal: this.selectedDataSetVal,
              childFileData: this.childFileData,
            });
            this._spinner.stopSpinner();
          },
          (err: Error) => {
            this._spinner.stopSpinner();
            this._toaster.error('Error fetching text', 'Text Analysis');
          }
        );
    }
  };

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
