import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root",
})
export class TextAnalysisService {
  serverBaseUrl: string = environment.serverBaseUrl;

  constructor(private _http: HttpClient) { }

  /**
   * fetches the list of experiments created by the user
   * @param projectId
   * @returns
   */
  experimentList(projectId: string): Observable<any> {
    return this._http.get(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/textAnalysis/experiments`
    );
  }

  /**
   * fetches the text data from the respective column from respective file.
   * @param projectId
   * @param featureName
   * @param fileName
   * @returns
   */
  listFeatureDetails(
    projectId: string,
    featureName: string,
    fileName: string
  ): Observable<any> {
    return this._http.get(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/textAnalysis/feature`,
      {
        params: { featureName: featureName, fileName: fileName },
      }
    );
  }

  /**
   * deletes the experiment
   * @param projectId
   * @returns
   */
  deleteExperiment(projectId: string, experimentId: string): Observable<any> {
    return this._http.delete(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/textAnalysis/experiments`,
      { params: { experimentId: experimentId } }
    );
  }

  /**
   * Fetches the taexperiment details
   * @param taExperimentId
   * @returns
   */
  taExperiment(taExperimentId: string): Observable<any> {
    return this._http.get(
      `${this.serverBaseUrl}/api/v2/textAnalysis/${taExperimentId}`
    );
  }

  textCleaning(inputData: any, taskConfig: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/textCleaning`,
      {
        data: inputData,
        taskConfig: taskConfig,
      }
    );
  }

  pos(inputData: any, taskConfig: any, renderType: any) {
    return this._http.post(`${this.serverBaseUrl}/api/v2/textAnalysis/pos`, {
      data: inputData,
      taskConfig: taskConfig,
      renderType: renderType,
    });
  }

  ner(inputData: any, taskConfig: any, renderType: any) {
    return this._http.post(`${this.serverBaseUrl}/api/v2/textAnalysis/ner`, {
      data: inputData,
      taskConfig: taskConfig,
      renderType: renderType,
    });
  }

  sentimentAnalysis(sentimentAnalysisParams: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/sentimentAnalysis`,
      {
        data: sentimentAnalysisParams.data,
        taskConfig: sentimentAnalysisParams.taskConfig,
      }
    );
  }

  textSummarization(summarizeParam: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/textSummarization`,
      {
        data: summarizeParam.data,
        taskConfig: summarizeParam.taskConfig,
      }
    );
  }

  wordFrequency(configData: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/wordFrequency`,
      {
        data: configData,
      }
    );
  }

  wordEmbedding(wordEmbeddingParam: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/wordEmbedding`,
      {
        data: wordEmbeddingParam.data,
        taskConfig: wordEmbeddingParam.taskConfig,
      }
    );
  }

  ruleBasedMatch(inputData: any, taskConfig: any, renderType: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/ruleBasedMatch`,
      {
        data: inputData,
        taskConfig: taskConfig,
        renderType: renderType,
      }
    );
  }

  saveTextAnalysis(dataConfig: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/saveAnalysis`,
      { ...dataConfig }
    );
  }

  saveApplyTextAnalysis(dataConfig: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/textAnalysis/apply/saveAnalysis`,
      { ...dataConfig }
    );
  }
}
