import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { timeStamp } from "console";
import { ToastrService } from "ngx-toastr";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { LocalStorageService } from "src/app/shared/local-storage.service";
import { SpinnerService } from "src/app/shared/spinner.service";
import { UtilsService } from "src/app/shared/utils.service";
import { PipelineSettingsDialogComponent } from "../../pipeline-settings-dialog/pipeline-settings-dialog.component";
import { TaDataStoreService } from "../../ta-datastore.service";
import { TextAnalysisService } from "../../text-analysis.service";

@Component({
  selector: "app-word-embedding",
  templateUrl: "./word-embedding.component.html",
  styleUrls: ["./word-embedding.component.scss"],
})
export class WordEmbeddingComponent implements OnInit {
  @Input() operation: any;

  @Output() onRemove = new EventEmitter();

  /**
   * models array
   */
  modelsList = [
    {
      modelName: "Word2Vec",
      modelKey: "word2vec",
    },
    {
      modelName: "Glove",
      modelKey: "Glove",
    },
  ];

  /**
   * algorithms array
   */
  trainingAlgorithmList = [
    {
      algorithmName: "CBOW",
      value: "0",
    },
    {
      algorithmName: "skip-gram",
      value: "1",
    },
  ];

  /**
   * functionality array
   */
  functionsList = [
    {
      functionName: "Similarity Score",
      value: "similarityscore",
    },
    {
      functionName: "Most Similar Word",
      value: "mostsimilarword",
    },
    {
      functionName: "Output Word",
      value: "outputword",
    },
  ];

  binColor: string = "basic";

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  displayedColumns: Array<string> = [];

  showTextError: Boolean = false;

  wordVectorSliderVal: Number = 100;
  learningRate: any = 0.05;
  wordEmbedding: any = {
    input: null,
  };

  showTableOutput: Boolean = false;

  showTextOutput: Boolean = false;

  output: any;
  project: any;
  user: any;
  inputData: any;
  tableHeading: string = "Score";

  wordEmbeddingModelControl = new FormControl("", Validators.required);
  wordEmbeddingTrainingAlgoControl = new FormControl("", Validators.required);
  wordEmbeddingFunctionsControl = new FormControl("", Validators.required);
  input1FormControl = new FormControl("", Validators.required);
  input2FormControl = new FormControl("", Validators.required);

  wordEmbeddingPipelineSettingsList: any = [
    {
      pipeSettingName: "Create vectors as new feature(s)",
      pipeSettingTag: "vector",
    },
  ];

  wordEmbeddingPipelineSettingValue: any;
  functions: { functionName: string; value: string }[];

  constructor(
    private _dialog: MatDialog,
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _taDataStore: TaDataStoreService,
    private _textAnalysis: TextAnalysisService,
    public _utils: UtilsService
  ) {
    // this._spinner.showSpinner();
    this.project = this._localStorage.getItem("project");
    this.user = this._localStorage.getItem("user");
    this.displayedColumns = ["word", "score"];
    // reset the form controls whenever the training algorithm is changed.
    this.wordEmbeddingTrainingAlgoControl.valueChanges.subscribe((algo) => {
      this.resetControls();
    });
    // reset the input form control whenever a diff method is selected.
    this.wordEmbeddingFunctionsControl.valueChanges.subscribe(
      (selectedMethod) => {
        selectedMethod != "similarityscore" && this.input2FormControl.reset();
      }
    );
  }

  ngOnInit(): void {
    this.inputData = this._taDataStore.getInputData(this.operation);
    // this._spinner.stopSpinner();
    this.setData();
    this._taDataStore.onSubmitEventSubject.subscribe((val) => {
      this.inputData = this._taDataStore.getInputData(this.operation);
    });
  }

  //function to set db data to component
  setData(): void {
    let loadData = this._taDataStore.getAllOperationData();
    loadData.forEach((loadVal) => {
      if (loadVal.uid == this.operation.uid) {
        if (loadVal.taskConfig.operationParameters) {
          if (loadVal.taskConfig.operationParameters.addFeature.length) {
            this.wordEmbeddingPipelineSettingValue =
              loadVal.taskConfig.operationParameters.addFeature;
          }
          this.wordEmbeddingModelControl.setValue(
            loadVal.taskConfig.operationParameters.model
          );
          this.wordVectorSliderVal =
            loadVal.taskConfig.operationParameters.wordVector || 100;
          if (
            loadVal.taskConfig.operationParameters.hasOwnProperty(
              "learningRate"
            )
          ) {
            this.learningRate =
              loadVal.taskConfig.operationParameters.learningRate;
          }
          if (loadVal.taskConfig.operationParameters.trainingAlgorithm) {

            let item = this.trainingAlgorithmList.filter((algorithm) => {
              if (
                loadVal.taskConfig.operationParameters.trainingAlgorithm ==
                algorithm.value
              ) {
                return algorithm;
              }
            });
            this.wordEmbeddingTrainingAlgoControl.setValue(item[0].value);
          }
          if (loadVal.taskConfig.operationParameters.method === 'Glove') {
            this.functions = this.functionsList.filter((item) => {
              return item.value === 'mostsimilarword';
            });
          } else {
            this.functions = this.functionsList;
          }
          this.wordEmbeddingFunctionsControl.setValue(
            loadVal.taskConfig.operationParameters.method
          );
          if (
            loadVal.taskConfig.operationParameters.input &&
            typeof loadVal.taskConfig.operationParameters.input === 'object'
          ) {
            this.input1FormControl.setValue(
              loadVal.taskConfig.operationParameters.input[0]
            );
            this.input2FormControl.setValue(
              loadVal.taskConfig.operationParameters.input[1]
            );
          } else {
            this.input1FormControl.setValue(
              loadVal.taskConfig.operationParameters.input
            );
          }
        }
      }
    });
  }

  onModelChange(method) {
    if (method === 'Glove') {
      this.functions = this.functionsList.filter((item) => {
        return item.value === 'mostsimilarword';
      });
    } else {
      this.functions = this.functionsList;
    }
  }

  // function to reset the input form controls
  resetControls(): void {
    this.wordEmbeddingFunctionsControl.reset();
    this.input1FormControl.reset();
    this.input2FormControl.reset();
  }

  /**
   * function to pass the value to thumb of the slider.
   * @param value
   * @returns
   */
  wordVectorSliderLabel(value: Number) {
    return value;
  }

  mouseOverBin(): void {
    this.binColor = "warn";
  }

  mouseLeaveBin(): void {
    this.binColor = "basic";
  }

  /**
   * function to remove operation
   */
  removeOperation(): void {
    //Remove the operation data from data store
    this._taDataStore.removeOperation(this.operation);
    //Emit the remove event ot parent
    this.onRemove.emit(this.operation);
  }

  //function to open the pipeline settings dialog
  showPipelineSetting(): void {
    /**
     * Opens the dialog with form
     */
    let pipelineSettingsDialog = this._dialog.open(
      PipelineSettingsDialogComponent,
      {
        minWidth: "50vw",
        data: {
          list: this.wordEmbeddingPipelineSettingsList,
          selectedVal: this.wordEmbeddingPipelineSettingValue,
        },
      }
    );

    pipelineSettingsDialog.afterClosed().subscribe((data) => {
      if (data && this.output !== undefined) {
        this.wordEmbeddingPipelineSettingValue = data.featureArray;
        let inputData = this._taDataStore.getSavedOperationOutput(this.operation.uid,);
        inputData.taskConfig[0].operationParameters.addFeature = this.wordEmbeddingPipelineSettingValue;
        this._taDataStore.saveOperationOutput({
          uid: this.operation.uid,
          operation: this.operation.operation,
          output: this.output,
          taskConfig: inputData.taskConfig,
        });
      } else if (data) {
        this.wordEmbeddingPipelineSettingValue = data.featureArray;
      }
    });
  }

  onSubmit = (
    modelData: FormControl,
    sliderVal: Number,
    trainingData: FormControl,
    methodData: FormControl,
    input1: FormControl,
    input2: FormControl
  ) => {
    this.showTableOutput = false;
    this.showTextOutput = false;
    // vm.loaderText = "Please Wait, doing word embedding";
    // vm.showLoading = true;
    this._spinner.showSpinner();
    this.showTextError = false;
    let inputData = this._taDataStore.getInputData(this.operation);
    inputData = inputData?.output;
    //checking if the input data is empty
    if (!inputData) {
      this.showTextError = true;
      // vm.showLoading = false;
      this._spinner.stopSpinner();
      return;
    }

    if (input1.value && input2.value) {
      this.wordEmbedding.input = [input1.value, input2.value];
    } else if (input1.value) {
      this.wordEmbedding.input = input1.value;
    } else {
      this.wordEmbedding.input = input2.value;
    }

    //preparing request object for wordEmbedding api
    let wordEmbeddingParam = {
      data: inputData,
      taskConfig: [
        {
          operationName: "wordEmbedding",
          operationParameters: {
            model: modelData.value,
            wordVector: sliderVal || 100,
            trainingAlgorithm: parseInt(trainingData.value) || 0,
            method: methodData.value,
            input: this.wordEmbedding.input || null,
            addFeature: this.wordEmbeddingPipelineSettingValue || [],
            learningRate: this.learningRate,
          },
        },
      ],
    };
    if (modelData.value == "Glove") {
      delete wordEmbeddingParam.taskConfig[0].operationParameters[
        "trainingAlgorithm"
      ];
    } else {
      delete wordEmbeddingParam.taskConfig[0].operationParameters[
        "learningRate"
      ];
    }
    this._textAnalysis
      .wordEmbedding(wordEmbeddingParam)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (
            typeof res.data.data === "string" &&
            res.data.data === "word is not in vocab"
          ) {
            this.showTableOutput = false;
            this.showTextOutput = true;
            this.output = "word is not in vocab";
          } else {
            if (
              wordEmbeddingParam.taskConfig[0].operationParameters.method ==
              "similarityscore"
            ) {
              this.showTextOutput = true;
              this.output =
                "The Similarity Score is " + res.data.data.toFixed(2);
            } else {
              this.showTableOutput = true;
              this.output = res.data.data;
              if (this.output[0]?.score) {
                this.tableHeading = "Score";
              } else {
                this.tableHeading = "Probability";
              }
              this.dataSource.data = res.data.data;
              this.dataSource._updateChangeSubscription();
            }
          }
          //save result back to the service
          this._taDataStore.saveOperationOutput({
            uid: this.operation.uid,
            operation: "wordEmbedding",
            output: this.output,
            taskConfig: wordEmbeddingParam.taskConfig,
          });
          // vm.showLoading = false;
          // $rootScope.$broadcast("onSubmit");
          this._spinner.stopSpinner();
          this._taDataStore.broadcastSubmitEvent("updateInputBar");
        },
        (err: Error) => {
          this.showTableOutput = false;
          this.showTextOutput = false;
          // toaster.pop('error', $scope.app.name, 'Error Summarizing text', 3000);
          // vm.showLoading = false;
          this._spinner.stopSpinner();
          this._toaster.error("Server Error", "Error performing word embedding");
        }
      );
    this._spinner.stopSpinner();
  };

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
