import { TextAnalysisService } from "./../text-analysis.service";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { SafeResourceUrl, DomSanitizer } from "@angular/platform-browser";
import { ToastrService } from "ngx-toastr";
import { Subject } from "rxjs";
import { LocalStorageService } from "src/app/shared/local-storage.service";
import { SpinnerService } from "src/app/shared/spinner.service";
import { SchedularService } from "../../dashboard/schedular/schedular.service";
import { takeUntil } from "rxjs/operators";
import { TaDataStoreService } from "../ta-datastore.service";
import { Router } from "@angular/router";

const swal = require("sweetalert");

@Component({
  selector: 'app-experiments-list',
  templateUrl: './experiments-list.component.html',
  styleUrls: ['./experiments-list.component.scss'],
})
export class ExperimentsListComponent implements OnInit {
  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  @ViewChild('exListPaginator') set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  @ViewChild('taSort') set sort(value: MatSort) {
    this.dataSource.sort = value;
  }
  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  showSpinner: boolean = false;
  user: any;
  project: any;
  experimentMode: any;
  experimentsArray: Array<any>;

  constructor(
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _dialog: MatDialog,
    private _sanitizer: DomSanitizer,
    private _textAnalysis: TextAnalysisService,
    private _taDataStore: TaDataStoreService,
    private _schedular: SchedularService,
    private route: Router
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.displayedColumns = [
      'srNo',
      'experimentName',
      'experimentId',
      'status',
      'createdAt',
      'actions',
    ];
  }

  ngOnInit(): void {
    // this._spinner.showSpinner();
    this.getExperimentsList();
  }

  getExperimentsList(): void {
    this._textAnalysis
      .experimentList(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this._spinner.stopSpinner();
          if (res) {
            this.experimentsArray = res;
            this.dataSource.data = res;
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.dataSource._updateChangeSubscription();
          } else {
            this._toaster.error('Cannot fetch experiments', 'Drift Settings');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Text Analysis');
        }
      );
  }

  /**
   * function to delete the experiment from the db
   * @param experimentsData
   */
  deleteHandler(experimentsData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this experiment!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.showSpinner = true;
        this._textAnalysis.deleteExperiment(this.project._id, experimentsData._id).subscribe(
          (res: any) => {
            if (res) {
              this.experimentsArray = this.experimentsArray.filter(
                (item) => item._id !== experimentsData._id
              );
              this.dataSource.data = this.experimentsArray;
              this.dataSource._updateChangeSubscription();
              this._toaster.success(
                `${experimentsData.experimentName} deleted successfully`,
                'Text Analysis'
              );
            } else {
              this._toaster.error('Cannot delete the experiment', 'Text Analysis');
            }
            this.showSpinner = false;
          },
          (err: Error) => {
            this.showSpinner = false;
            this._toaster.error('Server Error', 'Text Analysis');
          }
        );
      }
    });
  }

  clearTaData(): void {
    this._taDataStore.cleartaData();
  }

  //deleting the experiment mode key from localstorage and then routing to the create-exp page
  gotoCreate(): void {
    this._localStorage.deleteItem('experimentMode');
    this.clearTaData();
    this._spinner.stopSpinner();
    this.route.navigate(['/project/textanalysis/create-experiment']);
  }

  //function to call service method to store the model data for exeriments
  setExperimentsData(operationsData: any, mode?: any): void {
    this.clearTaData();
    //checking the mode for showing or hiding the save exp btn
    if (mode) {
      this.experimentMode = mode;
      this._localStorage.setItem('experimentMode', this.experimentMode);
    } else {
      this._localStorage.deleteItem('experimentMode');
    }

    // this.onChangeDataset(operationsData.fileId, true, operationsData.feature);
    let inputDataForOperations: any = {
      uid: 1,
      featureName: operationsData.feature,
      fileId: operationsData.fileId,
      operation: 'inputData',
      output: operationsData.inputText,
      experimentName: operationsData.experimentName,
    };

    // if (this.selectedDataSetVal) {
    //   inputDataForOperations.fileName = this.selectedDataSetVal.name;
    // }
    this._taDataStore.setExperimentsData(inputDataForOperations);
    operationsData.taskConfig.forEach((operation) => {
      let dataToSave = {
        uid: operation.uid,
        operation: operation.operationName,
        taskConfig: operation.parameters,
      };
      this._taDataStore.setExperimentsData(dataToSave);
    });
    this._spinner.stopSpinner();
    //nevigating to create-exp page with expId as stateparam
    this.route.navigate(['/project/textanalysis/create-experiment'], {
      queryParams: { experimentId: operationsData._id },
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
