import {
  Component,
  OnInit,
  Input,
  ElementRef,
  ViewChild,
  HostListener,
  Output,
  EventEmitter,
} from "@angular/core";
import { Options } from "sortablejs";

@Component({
  selector: "app-operations-list",
  templateUrl: "./operations-list.component.html",
  styleUrls: ["./operations-list.component.scss"],
})
export class OperationsListComponent implements OnInit {
  @Input() operationList: any;

  @Output() isToolBarSticky = new EventEmitter();

  @ViewChild("operationsToolBar", { read: ElementRef, static: true })
  operationsToolBar: ElementRef;

  @HostListener("window:scroll", ["$event"]) onWindowScroll() {
    this.myFunction(window.pageYOffset);
  }

  sourceOptions: Options = {
    group: {
      name: "clone-group",
      pull: "clone",
      put: false,
    },
  };

  toolbarOffset: any;

  setStickyClass = false;

  opToolbar: any;
  opUiContainer: any;
  sticky: any;

  constructor() {}

  ngOnInit(): void {
    // this.opToolbar = document.getElementById("operationList");
    // this.sticky = this.opToolbar.offsetTop;
    this.sticky =
      this.operationsToolBar.nativeElement.getBoundingClientRect().top;
  }

  myFunction(offsetVal: any): void {
    if (offsetVal >= this.sticky) {
      this.setStickyClass = true;
      // this.operationsToolBar.nativeElement.classList.add("sticky-ta-toolbar");
      // this.opUiContainer.classList.add("extra-height");
      this.isToolBarSticky.emit("true");
    } else {
      this.setStickyClass = false;
      // this.operationsToolBar.nativeElement.classList.remove(
      //   "sticky-ta-toolbar"
      // );
      // this.opUiContainer.classList.remove("extra-height");
      this.isToolBarSticky.emit("false");
    }
  }
}
