import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DomSanitizer } from '@angular/platform-browser';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-eda-report',
  templateUrl: './eda-report.component.html',
  styleUrls: ['./eda-report.component.scss'],
  // encapsulation: ViewEncapsulation.ShadowDom,
})
export class EdaReportComponent implements OnInit {
  serverBaseUrl: string = environment.serverBaseUrl;

  showSpinner: boolean = true;
  safeUrl: any;
  edaData: any;

  /**
   * Receives the injected data in the modal
   * @param dialogData
   */
  constructor(@Inject(MAT_DIALOG_DATA) private dialogData: any, private _sanitizer: DomSanitizer) {}

  ngOnInit(): void {
    this.showSpinner = true;
    this.edaData = this.dialogData.edaReportData;
    switch (this.dialogData.moduleName) {
      case 'data-set':
        this.safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(
          `${this.serverBaseUrl}/api/v2/projects/${this.edaData.projectId}/data/${this.edaData.dataId}/report`
        );
        this.showSpinner = false;
        break;
      case 'reports':
      case 'eda':
        this.safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(
          `${this.serverBaseUrl}/api/v2/projects/${this.edaData.projectId}/eda/${this.edaData.edaId}/advedainfo?currentEdaId=${this.edaData.currentEdaId}&currentPipelineFilename=${this.edaData.currentPipelineFilename}`
        );
        this.showSpinner = false;
        break;
      case 'cluster':
        this.safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(
          `${this.serverBaseUrl}/api/projects/${this.edaData.projectId}/clustering/visualize/scatterplot?filename=${this.edaData.fileName}`
        );
        this.showSpinner = false;
        break;
      default:
        this.safeUrl = '';
    }
  }
}
