import { Component, Inject, OnInit } from "@angular/core";
import { FormControl, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { LocalStorageService } from "src/app/shared/local-storage.service";
import { SpinnerService } from "src/app/shared/spinner.service";
import { TaDataStoreService } from "../ta-datastore.service";
import { TextAnalysisService } from "../text-analysis.service";
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { CreateExperimentComponent } from "../create-experiment/create-experiment.component";
import { SocketIoService } from "src/app/core/socket-io.service";
import { DomSanitizer } from "@angular/platform-browser";

@Component({
  selector: "app-text-analysis-dialog",
  templateUrl: "./text-analysis-dialog.component.html",
  styleUrls: ["./text-analysis-dialog.component.scss"],
})
export class TextAnalysisDialogComponent implements OnInit {
  saveDialogControl = new FormControl("", Validators.required);
  project: any;
  user: any;
  operationData: any;
  showSaveApplybtn: Boolean = false;
  inputData: string;
  inputFileId: any;
  featureName: string;
  inputFileName: string;
  dataSetDbObj: any;
  childFileData: any;
  graphContent: any;
  dialogTitle: string = null;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();
  showSaveError: boolean = false;
  showExecutionError = false;
  downloadIconColor: string = "basic";
  graphFileUrl: any;
  graphFileName: string = null;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: { data: any; showGraph: string },
    private route: Router,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _taDataStore: TaDataStoreService,
    private _textAnalysis: TextAnalysisService,
    private _toaster: ToastrService,
    private _textAnalysisDialog: MatDialogRef<CreateExperimentComponent>,
    private _socket: SocketIoService,
    private _sanitizer: DomSanitizer
  ) {
    this.project = this._localStorage.getItem("project");
    this.user = this._localStorage.getItem("user");
    this.downloadGraph(data.showGraph);
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    if (!this.data.showGraph) {
      this.dialogTitle = "Save Experiment";
      this.initSocket();
      this.operationData = this._taDataStore.getAllOperationData(true);
      if (this.operationData.length < 2) {
        this.showSaveError = true;
        this.showExecutionError = false;
      } else if (!this.operationData.every((val) => val.output)) {
        this.showSaveError = true;
        this.showExecutionError = true;
      } else {
        this.showSaveError = false;
        this.showExecutionError = false;
        this.operationData.some((val) => {
          if (val.operation === "inputData") {
            if (!val.featureName) {
              this.showSaveApplybtn = false;
              this.featureName = null;
            } else {
              this.showSaveApplybtn = true;
              this.featureName = val.featureName;
            }
            this.inputData = val.output;
            this.inputFileName = val.fileName;
            this.inputFileId = val.fileId;
            this.dataSetDbObj = val.dataSetVal;
            this.childFileData = val.childFileData;
          }
        });
      }
    } else {
      if (this.data.showGraph == "pos") {
        this.dialogTitle = "Dependency Graph";
      } else if (this.data.showGraph == "rbm") {
        this.dialogTitle = "Matching Visualization";
      } else if (this.data.showGraph == "ner") {
        this.dialogTitle = "Named Entity Visualization";
      } else {
        this.dialogTitle = "Save Experiment";
      }

      this.graphContent = this._sanitizer.bypassSecurityTrustHtml(
        this.data.data
      );
    }
    this._spinner.stopSpinner();
  }

  mouseOverDownload(): void {
    this.downloadIconColor = "primary";
  }

  mouseLeaveDownload(): void {
    this.downloadIconColor = "basic";
  }

  /**
   * function to download graph
   */
  downloadGraph = function (graphType: string) {
    this.graphFileName = graphType + "_graph.html";
    var file = new Blob([this.data.data], { type: "application/html" });
    this.graphFileUrl = this._sanitizer.bypassSecurityTrustResourceUrl(
      window.URL.createObjectURL(file)
    );
  };

  saveExperiment(expName: FormControl): void {
    this._spinner.showSpinner();
    //taking all data except the input data.
    this.operationData = this._taDataStore.getAllOperationData();
    let operationConfig = this.operationData.map((item) => {
      return item.taskConfig[0];
    });
    let operation = [];
    this.operationData.map((item) => {
      var data = {
        uid: item.uid,
        operationName: item.operation,
        parameters: item.taskConfig[0],
      };
      operation.push(data);
    });
    let dataConfig: any = {
      experimentName: expName.value,
      projectId: this.project._id,
      inputText: this.inputData,
      taskConfig: operation,
    };

    dataConfig.fileId = this.inputFileId ? this.inputFileId : null;
    dataConfig.feature = this.featureName ? this.featureName : null;
    // dataConfig.experimentId = $stateParams.experimentId ? $stateParams.experimentId
    //   : null;
    this._textAnalysis
      .saveTextAnalysis(dataConfig)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (resp: any) => {
          expName.setValue("");
          this._textAnalysisDialog.close();
          this._spinner.stopSpinner();
          this.route.navigate(["/project/textanalysis"]);
          this._spinner.stopSpinner();
          this._toaster.success(
            "Text analysis saved successfully",
            "Text Analysis"
          );
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error("Server Error", "Error saving text analysis");
        }
      );
  }

  saveAndApplyExperiment(expName: FormControl): void {
    this._spinner.showSpinner();
    //taking all data except the input data.
    this.operationData = this._taDataStore.getAllOperationData();
    let operationConfig = this.operationData.map((item) => {
      return item.taskConfig[0];
    });
    let operation = [];
    this.operationData.map((item) => {
      var data = {
        uid: item.uid,
        operationName: item.operation,
        parameters: item.taskConfig[0],
      };
      operation.push(data);
    });

    let dataConfig: any = {
      experimentName: expName.value,
      projectId: this.project._id,
      fileSource: "textAnalysis",
      dataGroupId: this.dataSetDbObj.datagroup_id,
      filePath: this.inputFileName,
      fileId: this.inputFileId,
      feature: this.featureName,
      inputText: this.inputData,
      fileSchema: this.dataSetDbObj.fileSchema,
      featureList: [this.featureName],
      taskConfig: operationConfig,
      operation: operation,
    };
    //need to do waiting for the best time to start
    if (this.childFileData?.hasOwnProperty('currentEdaId')) {
      dataConfig.parentPipelineFilename =
        this.childFileData.currentEdaId.tasks[0].currentPipelineFilename;
    } else {
      dataConfig.parentPipelineFilename = null;
    }
    // dataConfig.experimentId = $stateParams.experimentId? $stateParams.experimentId
    //   : null;
    this._textAnalysis
      .saveApplyTextAnalysis(dataConfig)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (resp: any) => {
          expName.reset();
          // vm.featureSelect = false;
          this._toaster.success("Text analysis started", "Text Analysis");
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error("Server Error", "Error saving text analysis");
        }
      );
  }

  /**
   * Initializes the socket to listen events when emmited
   */
  initSocket(): void {
    // Listens to node-red event
    this._socket.listen("textAnalysisDone").subscribe(
      (res: any) => {
        if (res.status.status === "completed") {
          this._textAnalysisDialog.close();
          this._spinner.stopSpinner();
          this.route.navigate(["/project/textanalysis"]);
          this._toaster.success(
            "Text analysis saved successfully",
            "Text Analysis"
          );
        } else if (res.status.status === "failed") {
          this._textAnalysisDialog.close();
          this._spinner.stopSpinner();
          this.route.navigate(["/project/textanalysis"]);
          this._toaster.error(
            res.status.message,
            "Text Analysis"
          );
        }
        this._spinner.stopSpinner();
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error("Socket Error", "Text Analysis");
      }
    );
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this._socket.removeListener("textAnalysisDone");
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
