import { map } from "rxjs/operators";
import {
  Component,
  EventEmitter,
  Input,
  IterableDiffers,
  OnInit,
  Output,
} from "@angular/core";
import { FormArray } from "@angular/forms";
import { Options } from "sortablejs";
import { SpinnerService } from "src/app/shared/spinner.service";

@Component({
  selector: "app-operations-ui-container",
  templateUrl: "./operations-ui-container.component.html",
  styleUrls: ["./operations-ui-container.component.scss"],
})
export class OperationsUiContainerComponent implements OnInit {
  @Input() selectedOperation: any = [];

  //options for sortable thing
  targetOptions: Options = {
    group: "clone-group",
  };

  iterableDiffer: any;

  constructor(
    private iterableDiffers: IterableDiffers,
    private _spinner: SpinnerService
  ) {
    this.iterableDiffer = iterableDiffers.find([]).create(null);
  }

  ngDoCheck() {
    let changes = this.iterableDiffer.diff(this.selectedOperation);
    if (changes) {
      this.selectedOperation.map((opVal) => {
        return changes.collection.filter((val) => {
          if (opVal.operation === val.operation) {
            if (!val.uid) {
              val.uid = Date.now();
            }
            val.isPanelExpanded = true;
          } else {
            val.isPanelExpanded = false;
          }
        });
      });
    }
  }

  ngOnInit(): void {}

  //Remove an operation
  onOperationRemoved = function (index) {
    this.selectedOperation.splice(index, 1);
  };
}
