import { FormControl, Validators } from "@angular/forms";
import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { TaDataStoreService } from "../../ta-datastore.service";
import { TextAnalysisService } from "../../text-analysis.service";
import { takeUntil } from "rxjs/operators";
import { Subject } from "rxjs";
import { SpinnerService } from "src/app/shared/spinner.service";
import { number } from "ngx-custom-validators/src/app/number/validator";
import { UtilsService } from "src/app/shared/utils.service";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-text-cleaning",
  templateUrl: "./text-cleaning.component.html",
  styleUrls: ["./text-cleaning.component.scss"],
})
export class TextCleaningComponent implements OnInit {
  @Input() operation: any;

  @Output() onRemove = new EventEmitter();

  /**
   * cleaning operations array
   */
  cleaningOperations = [
    {
      operationName: "Remove Numbers",
      operationKey: "removeNum",
    },
    {
      operationName: "Remove hyperlinks",
      operationKey: "removeHyper",
    },
    {
      operationName: "Remove emoticons/emojis",
      operationKey: "removeEmoticon",
    },
    {
      operationName: "Remove words",
      operationKey: "shortWord",
    },
    {
      operationName: "Expand contractions",
      operationKey: "expandCont",
    },
    {
      operationName: "Remove punctuations",
      operationKey: "removePunct",
    },
    {
      operationName: "Stemming",
      operationKey: "stemming",
    },
    {
      operationName: "Lemmatization",
      operationKey: "lemmatization",
    },
    {
      operationName:
        "Option To Delete Rows If Empty Ones Are Created After Cleaning",
      operationKey: "optionToDeleteRow",
    },
  ];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  testCleaningControl = new FormControl("", Validators.required);

  showRemoveWordsSlider: Boolean = false;

  showTextError: Boolean = false;

  showOutput: Boolean = false;

  binColor: string = "basic";

  copyColor: string = "basic";

  tooltipText: string = "Click to copy!";

  inputData: any;

  output: any;

  cleaningOperationsDbData: Array<any> = [];

  showValidationError: Boolean = false;

  countOfWordsToRemove: number = 1;

  constructor(
    private _taDataStore: TaDataStoreService,
    private _toaster: ToastrService,
    private _textAnalysis: TextAnalysisService,
    private _spinner: SpinnerService,
    public _utils: UtilsService
  ) {
    /**
     * setting slider to true when the remove words method is selected.
     */
    this.testCleaningControl.valueChanges.subscribe((selectedValue) => {
      if (this.testCleaningControl.value.includes("shortWord")) {
        this.showRemoveWordsSlider = true;
      } else {
        this.showRemoveWordsSlider = false;
      }
    });
  }

  ngOnInit(): void {
    this.inputData = this._taDataStore.getInputData(this.operation);
    this.setData();
    this._taDataStore.onSubmitEventSubject.subscribe((val) => {
      this.inputData = this._taDataStore.getInputData(this.operation);
    });
  }

  //function to set db data to component
  setData(): void {
    let loadData = this._taDataStore.getAllOperationData();
    loadData.forEach((loadVal) => {
      if (loadVal.uid == this.operation.uid) {
        if (loadVal.taskConfig.operationParameters) {
          loadVal.taskConfig.operationParameters.forEach((param) => {
            if (param.operationKey == "shortWord") {
              this.showRemoveWordsSlider = true;
              this.countOfWordsToRemove = param.threshold;
            }
            this.cleaningOperationsDbData.push(param.operationKey);
          });
          this.testCleaningControl.setValue(this.cleaningOperationsDbData);
        }
      }
    });
  }

  /**
   * function to pass the value to thumb of the slider.
   * @param value
   * @returns
   */
  removeWordsSliderLabel(value: number) {
    return value;
  }

  mouseOverBin(): void {
    this.binColor = "warn";
  }

  mouseLeaveBin(): void {
    this.binColor = "basic";
  }

  /**
   * function to remove operation
   */
  removeOperation(): void {
    //Remove the operation data from data store
    this._taDataStore.removeOperation(this.operation);
    //Emit the remove event ot parent
    this.onRemove.emit(this.operation);
  }

  mouseOverCopy(): void {
    this.copyColor = "primary";
  }

  mouseLeaveCopy(): void {
    this.copyColor = "basic";
  }

  //function to copy summarization output
  copyOutput = function () {
    let textToCopyElement = document.getElementById("cleaningOutput").innerText;
    let dummyElem = document.createElement("textarea");
    document.body.appendChild(dummyElem);
    dummyElem.value = textToCopyElement;
    dummyElem.select();
    document.execCommand("copy");
    document.body.removeChild(dummyElem);
    this.tooltipText = "Copied!";
  };

  onSubmit(data: FormControl, removeWordsCount: number): void {
    this._spinner.showSpinner();
    this.showOutput = false;
    if (removeWordsCount == undefined) {
      this.showValidationError = true;
      this._spinner.stopSpinner();
      return;
    } else if (removeWordsCount < 1) {
      this.showValidationError = true;
      this._spinner.stopSpinner();
      return;
    } else {
      this.showValidationError = false;
    }
    // function to map the selected operations
    let cleanArray = this.cleaningOperations.filter(function (item) {
      return data.value.some(function (doc) {
        return item.operationKey === doc;
      });
    });
    // function to add threshold if operation is short word
    let cleanOperation = cleanArray.map(function (e: any) {
      if (e.operationName == "Remove words") {
        // var o = Object.assign({}, e)
        e.threshold = Number(removeWordsCount);
        return e;
      } else {
        return e;
      }
    });
    let inputData = this._taDataStore.getInputData(this.operation);
    inputData = inputData?.output;
    // condition to check if textbox is empty
    if (!inputData) {
      this.showTextError = true;
      this._spinner.stopSpinner();
      return;
    } else {
      this.showTextError = false;
    }
    // this.popoverContent = "Click to Copy";
    // this.loaderText = "Please wait, Text cleaning is in progress.";
    // this.showLoading = true;
    var taskConfig = [
      {
        operationName: "textCleaning",
        operationParameters: cleanOperation,
      },
    ];

    // api call for output
    this._textAnalysis.textCleaning(inputData, taskConfig).subscribe(
      (res: any) => {
        this.showOutput = true;
        this.output = res.data.data;
        // store the data in the temporary service
        this._taDataStore.saveOperationOutput({
          uid: this.operation.uid,
          operation: this.operation.operation,
          output: this.output,
          taskConfig: taskConfig,
        });
        this._spinner.stopSpinner();
        // $rootScope.$broadcast("onSubmit");
        this._taDataStore.broadcastSubmitEvent("updateInputBar");
      },
      (err: Error) => {
        this.showOutput = false;
        this._spinner.stopSpinner();
        this._toaster.error("Server Error", "Error performing text cleaning");
      }
    );
  }

  // /**
  //  * Unsubscribes the subscriptions after destroy
  //  */
  // ngOnDestroy() {
  //   this.unsubscribe$.next(false);
  //   this.unsubscribe$.unsubscribe();
  // }
}
