import { TaDataStoreService } from "./../ta-datastore.service";
import { SortableOptions } from "sortablejs";
import {
  Component,
  ElementRef,
  HostListener,
  OnInit,
  ViewChild,
} from "@angular/core";
import { Options } from "sortablejs";
import { FormArray } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { TextAnalysisDialogComponent } from "../text-analysis-dialog/text-analysis-dialog.component";
import { LocalStorageService } from "src/app/shared/local-storage.service";
import { SpinnerService } from "src/app/shared/spinner.service";

@Component({
  selector: "app-create-experiment",
  templateUrl: "./create-experiment.component.html",
  styleUrls: ["./create-experiment.component.scss"],
})
export class CreateExperimentComponent implements OnInit {
  @ViewChild("opUiContainer", { read: ElementRef, static: true })
  opUiContainer: ElementRef;

  operations = [
    {
      title: "Text Cleaning/ Transformation",
      operation: "cleaningTransformation",
      class: "fas fa-magic iconfont",
      isPanelExpanded: false,
    },
    {
      title: "POS Tagging",
      operation: "posTagging",
      class: "fas fa-tags iconfont",
      isPanelExpanded: false,
    },
    {
      title: "Named Entity Recognition",
      operation: "ner",
      class: "fas fa-user-tag iconfont",
      isPanelExpanded: false,
    },
    {
      title: "Sentiment Analysis",
      operation: "sentimentAnalysis",
      class: "fas fa-circle-notch iconfont",
      isPanelExpanded: false,
    },
    {
      title: "Text Summarization",
      operation: "textSummarization",
      class: "fas fa-file-word iconfont",
      isPanelExpanded: false,
    },
    {
      title: "Word Frequency",
      operation: "wordFrequency",
      class: "fa fa-object-group iconfont",
      isPanelExpanded: false,
    },
    {
      title: "Word Embedding",
      operation: "wordEmbedding",
      class: "fa fa-language iconfont",
      isPanelExpanded: false,
    },
    {
      title: "Rule Based Matching",
      operation: "ruleBasedMatch",
      class: "fa fa-window-restore iconfont",
      isPanelExpanded: false,
    },
  ];

  selectedOperationsArray = [];

  taDataArray: any;

  modeType: any;

  checkExecuteSequence: boolean = false;

  targetOptions: Options = {
    group: "clone-group",
  };

  constructor(
    private _dialog: MatDialog,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _taDataStore: TaDataStoreService
  ) {
    this.modeType = this._localStorage.getItem("experimentMode");
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    let taData = this._taDataStore.getAllOperationData();
    if (taData.length) {
      taData.forEach((data) => {
        this.operations.forEach((operation) => {
          if (data.operation == operation.operation) {
            let temp: any = Object.assign({}, operation);
            temp.uid = data.uid;
            this.selectedOperationsArray.push(temp);
            this.checkExecuteSequence = true;
            // this.showSaveBtn = false;
          }
        });
      });
    } else {
      this.selectedOperationsArray.length = 0;
    }
    this._spinner.stopSpinner();
  }

  setOperationContainerUiMargin(isSticky: any): void {
    if (isSticky == "true") {
      this.opUiContainer.nativeElement.classList.add("extra-height");
    } else {
      this.opUiContainer.nativeElement.classList.remove("extra-height");
    }
  }

  openSaveDialog(): void {
    /**
     * Opens the dialog with form
     */
    let saveDialog = this._dialog.open(TextAnalysisDialogComponent, {
      width: "60%",
      data: {
        data: null,
        showGraph: null,
      },
    });

    saveDialog.afterClosed().subscribe((data) => {
      if (data) {
        //navigate to experiments list page after successful completion
      }
    });
  }
}
