import { takeUntil } from "rxjs/operators";
import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl, Validators } from "@angular/forms";
import { ToastrService } from "ngx-toastr";
import { Subject } from "rxjs";
import { DashboardService } from "src/app/routes/dashboard/dashboard.service";
import { LocalStorageService } from "src/app/shared/local-storage.service";
import { SpinnerService } from "src/app/shared/spinner.service";
import { TaDataStoreService } from "../../ta-datastore.service";
import { TextAnalysisService } from "../../text-analysis.service";
import { MatTableDataSource } from "@angular/material/table";
import { PipelineSettingsDialogComponent } from "../../pipeline-settings-dialog/pipeline-settings-dialog.component";
import { MatDialog } from "@angular/material/dialog";
import { TextAnalysisDialogComponent } from "../../text-analysis-dialog/text-analysis-dialog.component";
import { UtilsService } from "src/app/shared/utils.service";

@Component({
  selector: "app-pos-tagging",
  templateUrl: "./pos-tagging.component.html",
  styleUrls: ["./pos-tagging.component.scss"],
})
export class PosTaggingComponent implements OnInit {
  @Input() operation: any;

  @Output() onRemove = new EventEmitter();

  /**
   * tag input list
   */
  postTagList: Array<any> = [
    {
      tagsName: "Adjective",
      tagsValue: "ADJ",
    },
    {
      tagsName: "Adposition",
      tagsValue: "ADP",
    },
    {
      tagsName: "Adverb",
      tagsValue: "ADV",
    },
    {
      tagsName: "Auxiliary verb",
      tagsValue: "AUX",
    },
    {
      tagsName: "Coordinating conjunction",
      tagsValue: "CONJ",
    },
    {
      tagsName: "Determiner",
      tagsValue: "DET",
    },
    {
      tagsName: "Interjection",
      tagsValue: "INTJ",
    },
    {
      tagsName: "Noun",
      tagsValue: "NOUN",
    },
    {
      tagsName: "Numeral",
      tagsValue: "NUM",
    },
    {
      tagsName: "Particle",
      tagsValue: "PART",
    },
    {
      tagsName: "Pronoun",
      tagsValue: "PRON",
    },
    {
      tagsName: "Proper noun",
      tagsValue: "PROPN",
    },
    {
      tagsName: "Punctuation",
      tagsValue: "PUNCT",
    },
    {
      tagsName: "Subordinating conjunction",
      tagsValue: "SCONJ",
    },
    {
      tagsName: "Symbol",
      tagsValue: "SYM",
    },
    {
      tagsName: "Verb",
      tagsValue: "VERB",
    },
    {
      tagsName: "Other",
      tagsValue: "X",
    },
  ];

  /**
   * models list
   */
  modelList: Array<any>;

  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  displayedColumns: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();
  showSpinner: boolean = false;
  user: any;
  project: any;
  showError: boolean;
  showOutput: boolean = false;
  uploadModelIssueMsg: boolean = false;
  binColor: string = "basic";
  inputData: any;
  output: any;
  showTextError: Boolean = false;
  posModelControl = new FormControl("", Validators.required);
  posTagsControl = new FormControl("", Validators.required);
  graphIconColor: string = "basic";
  //default value for the pipeline setting modal
  pipelineSetting: any = {
    removeFeatureCheckBoxVal: false,
    setConcatenate: false,
    setCountTag: false,
  };

  posPipelineSettingsList: any = [
    {
      pipeSettingName: "Count the words that matched the tags",
      pipeSettingTag: "count",
    },
    {
      pipeSettingName: "Concatenate all the words with matched tags",
      pipeSettingTag: "concat",
    },
  ];

  posPipelineSettingValue: any;

  constructor(
    private _dialog: MatDialog,
    private _dashboardService: DashboardService,
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _taDataStore: TaDataStoreService,
    private _textAnalysis: TextAnalysisService,
    public _utils: UtilsService
  ) {
    // this._spinner.showSpinner();
    this.project = this._localStorage.getItem("project");
    this.user = this._localStorage.getItem("user");
    this.displayedColumns = ["Word", "Tag"];
  }

  ngOnInit(): void {
    this.inputData = this._taDataStore.getInputData(this.operation);
    this.gettaModelsList("pos");
    this.setData();
    this._taDataStore.onSubmitEventSubject.subscribe((val) => {
      this.inputData = this._taDataStore.getInputData(this.operation);
    });
  }

  // function to get all the models from the tamodel db
  gettaModelsList = (taskType: string) => {
    let foundModel = false;
    this._dashboardService
      .taModel(taskType)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (taModelList: any) => {
          this.modelList = taModelList;
          if (
            this.posModelControl.value &&
            this.posModelControl.value != "spacy-en_core_web_sm"
          ) {
            this.modelList.forEach((model) => {
              if (
                model.modelName === this.posModelControl.value ||
                model.oldModelFileName === this.posModelControl.value
              ) {
                this.posModelControl.setValue(JSON.stringify(model));
                foundModel = true;
              }
            });
            if (!foundModel) {
              this.uploadModelIssueMsg = true;
            }
          }
          this._spinner.stopSpinner();
        },
        (err: Error) => {
          this._toaster.error("Server Error", "Text Analysis");
          this._spinner.stopSpinner();
        }
      );
  };

  //function to set db data to component
  setData(): void {
    let loadData = this._taDataStore.getAllOperationData();
    loadData.forEach((loadVal) => {
      if (loadVal.uid == this.operation.uid) {
        if (loadVal.taskConfig.operationParameters) {
          this.posTagsControl.setValue(
            loadVal.taskConfig.operationParameters.keepTags
          );
          if (loadVal.taskConfig.operationParameters.addFeature.length) {
            this.posPipelineSettingValue =
              loadVal.taskConfig.operationParameters.addFeature;
          }
          this.pipelineSetting.removeFeatureCheckBoxVal =
            loadVal.taskConfig.operationParameters.removeFeature;
          this.posModelControl.setValue(
            loadVal.taskConfig.operationParameters.model
          );
        }
      }
    });
  }

  mouseOverBin(): void {
    this.binColor = "warn";
  }

  mouseLeaveBin(): void {
    this.binColor = "basic";
  }

  /**
   * function to remove operation
   */
  removeOperation(): void {
    //Remove the operation data from data store
    this._taDataStore.removeOperation(this.operation);
    //Emit the remove event to parent
    this.onRemove.emit(this.operation);
  }

  //function to open the pipeline settings dialog
  showPipelineSetting(): void {
    /**
     * Opens the dialog with form
     */
    let pipelineSettingsDialog = this._dialog.open(
      PipelineSettingsDialogComponent,
      {
        minWidth: "50vw",
        data: {
          list: this.posPipelineSettingsList,
          selectedVal: this.posPipelineSettingValue,
        },
      }
    );

    pipelineSettingsDialog.afterClosed().subscribe((data) => {
      if (data && this.output !== undefined) {
        this.posPipelineSettingValue = data.featureArray;
        let inputData = this._taDataStore.getSavedOperationOutput(this.operation.uid,);
        inputData.taskConfig[0].operationParameters.addFeature = this.posPipelineSettingValue;
        this._taDataStore.saveOperationOutput({
          uid: this.operation.uid,
          operation: this.operation.operation,
          output: this.output,
          taskConfig: inputData.taskConfig,
        });
      } else if (data) {
        this.posPipelineSettingValue = data.featureArray;

      }
    });
  }

  mouseOverGraph(): void {
    this.graphIconColor = "primary";
  }

  mouseLeaveGraph(): void {
    this.graphIconColor = "basic";
  }

  /**
   * function to compute output
   */
  onSubmit = function (
    type: any,
    modelData: FormControl,
    tagsData: FormControl
  ) {
    let renderType = null;
    if (type) {
      renderType = type;
    } else {
      // vm.loaderText = "Please wait, POS Tagging is in progress.";
      // vm.showLoading = true;
      this._spinner.showSpinner();
      renderType = "table";
    }
    let inputData = this._taDataStore.getInputData(this.operation);
    inputData = inputData?.output;
    if (!inputData) {
      this.showTextError = true;
      // vm.showLoading = false;
      this._spinner.stopSpinner();
      // vm.showPopupLoading = false;
      return;
    } else {
      this.showTextError = false;
    }
    let modelInfo = modelData.value;
    if (modelInfo != "spacy-en_core_web_sm") {
      modelInfo = modelData.value;
    }
    let taskConfig = [
      {
        operationName: "pos",
        operationParameters: {
          model: modelInfo.modelName || modelData.value,
          keepTags: tagsData.value || [],
          addFeature: this.posPipelineSettingValue || [],
          modelPath: `./textanalysis/models/${modelInfo.modelName}`,
          framework: modelInfo.frameWork,
          // removeFeature : vm.pipelineSetting.removeFeatureCheckBoxVal || false
        },
      },
    ];
    this._textAnalysis
      .pos(inputData, taskConfig, renderType)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this.showOutput = true;
          if (renderType == "html") {
            /**
             * Opens the dialog with form
             */
            let graphViewDialog = this._dialog.open(
              TextAnalysisDialogComponent,
              {
                width: "60%",
                data: {
                  data: res.data.data,
                  showGraph: "pos",
                },
              }
            );

            graphViewDialog.afterClosed().subscribe((data) => { });
          } else {
            this.output = res.data.data;
            this.dataSource.data = res.data.data;
            this.dataSource._updateChangeSubscription();
          }
          // store the data in the temporary service
          this._taDataStore.saveOperationOutput({
            uid: this.operation.uid,
            operation: this.operation.operation,
            output: this.output,
            taskConfig: taskConfig,
          });
          // vm.showLoading = false;
          this._spinner.stopSpinner();
          // vm.showPopupLoading = false;
          // $rootScope.$broadcast("onSubmit");
          this._taDataStore.broadcastSubmitEvent("updateInputBar");
        },
        (err: Error) => {
          this.showOutput = false;
          this._spinner.stopSpinner();
          this._toaster.error("Server Error", "Error performing pos tagging");
          // vm.showLoading = false;
          // vm.showPopupLoading = false;
          // toaster.pop('error', $scope.app.name, 'Error occured at output generation', 3000);
        }
      );
  };

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
