import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root",
})
export class TaDataStoreService {
  taData: Array<any> = [];
  validOperations: Array<any> = [
    "inputData",
    "cleaningTransformation",
    "textSummarization",
  ];

  public onSubmitEventSubject: BehaviorSubject<string> =
    new BehaviorSubject<string>("");

  constructor() { }

  //function to emit the event for updating the input bar for all the components
  public broadcastSubmitEvent(eventStatusTxt: string): void {
    this.onSubmitEventSubject.next(eventStatusTxt);
  }

  cleartaData() {
    this.taData = [];
  }

  //function to set db data to tadata array
  setExperimentsData(data) {
    this.taData.push(data);
  }

  //function to get saved operations output
  getSavedOperationOutput(uid) {
    let foundIndex = this.taData.findIndex((item) => item.uid == uid);
    return this.taData[foundIndex];
  }

  saveOperationOutput(opData) {
    let foundIndex = this.taData.findIndex((item) => item.uid == opData.uid);
    //add new data if inputData does not exist
    if (foundIndex == -1) {
      this.taData.push(opData);
    } else {
      //update inputdata if it exists
      this.taData[foundIndex] = opData;
    }
    this.getFirstInputData();
  }

  //Function to get operation data
  getFirstInputData() {
    return this.taData[0];
  }

  //Function to fetch inputData for the current operation based on previous operation
  getInputData(operation) {
    let inputItem = null;
    //always want input to be the taData[0] for word embedding, hence checking the operation and if it is wordembedding setting inputItem as taData[0].
    if (operation.operation === "wordEmbedding") {
      inputItem = this.taData[0];
    } else {
      let operationIndex = this.taData.findIndex((item) => {
        return item.uid == operation.uid;
      });
      let tempDataArray =
        operationIndex !== -1
          ? [...this.taData.slice(0, operationIndex)]
          : [...this.taData];
      tempDataArray.reverse().some((item) => {
        if (
          this.validOperations.includes(item.operation) &&
          item.uid != operation.uid
        ) {
          inputItem = item;
          return true;
        }
      });
    }
    return inputItem;
  }

  //function to remove an operation from data store
  removeOperation(operation) {
    this.taData = this.taData.filter((item) => {
      return item.uid !== operation.uid;
    });
  }

  //function to get taData
  getAllOperationData(removeFilter?: any) {
    let operationData = this.taData;
    if (!removeFilter) {
      operationData = operationData.filter((item) => {
        return item.operation !== "inputData";
      });
    }
    return operationData;
  }

  // function for re-ordering the operations
  orderOperationData(operationData, type) {
    if (type == "reoder" && this.taData.length > 1) {
      let input = this.taData[0];
      let result = [];
      result.push(input);
      operationData.forEach((element) => {
        let data = this.taData.find((item) => {
          return item.operation == element.operation;
        });
        if (data) {
          result.push(data);
        }
      });
      this.taData = result;
    }
  }
}
