import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { LocalStorageService } from "src/app/shared/local-storage.service";
import { SpinnerService } from "src/app/shared/spinner.service";
import { UtilsService } from "src/app/shared/utils.service";
import { TaDataStoreService } from "../../ta-datastore.service";
import { TextAnalysisService } from "../../text-analysis.service";

const Plotly = require("plotly.js-dist");

@Component({
  selector: "app-word-frequency",
  templateUrl: "./word-frequency.component.html",
  styleUrls: ["./word-frequency.component.scss"],
})
export class WordFrequencyComponent implements OnInit {
  @Input() operation: any;

  @Output() onRemove = new EventEmitter();

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  wordFrequency: Number = 20;
  showOutput: boolean = false;
  output: any;
  showValidationError: boolean = false;
  showTextError: boolean = false;
  binColor: string = "basic";
  project: any;
  user: any;
  inputData: any;

  constructor(
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _taDataStore: TaDataStoreService,
    private _textAnalysis: TextAnalysisService,
    public _utils: UtilsService
  ) {
    // this._spinner.showSpinner();
    this.project = this._localStorage.getItem("project");
    this.user = this._localStorage.getItem("user");
  }

  ngOnInit(): void {
    this.inputData = this._taDataStore.getInputData(this.operation);
    // this._spinner.stopSpinner();
    this.setData();
    this._taDataStore.onSubmitEventSubject.subscribe((val) => {
      this.inputData = this._taDataStore.getInputData(this.operation);
    });
  }

  //function to set db data to component
  setData(): void {
    let loadData = this._taDataStore.getAllOperationData();
    loadData.forEach((loadVal) => {
      if (loadVal.uid == this.operation.uid) {
        if (loadVal.taskConfig.operationParameters) {
          this.wordFrequency = loadVal.taskConfig.operationParameters.maxWords;
        }
      }
    });
  }

  /**
   * function to pass the value to thumb of the slider.
   * @param value
   * @returns
   */
  wordFrequencySliderLabel(value: number) {
    return value;
  }

  mouseOverBin(): void {
    this.binColor = "warn";
  }

  mouseLeaveBin(): void {
    this.binColor = "basic";
  }

  /**
   * function to remove operation
   */
  removeOperation(): void {
    //Remove the operation data from data store
    this._taDataStore.removeOperation(this.operation);
    //Emit the remove event ot parent
    this.onRemove.emit(this.operation);
  }

  onSubmit = function (wordFreqSliderVal: Number) {
    this._spinner.showSpinner();
    this.showOutput = false;
    if (wordFreqSliderVal == undefined) {
      this.showValidationError = true;
      this._spinner.stopSpinner();
      return;
    } else if (wordFreqSliderVal < 10) {
      this.showValidationError = true;
      this._spinner.stopSpinner();
      return;
    } else {
      this.showValidationError = false;
    }
    let fileDetails = this._taDataStore.getFirstInputData();
    if (!fileDetails) {
      this.showTextError = true;
      this._spinner.stopSpinner();
      return;
    } else {
      this.showTextError = false;
    }
    // this.loaderText = "Please wait, Word Frequency is in progress.";
    // this.showLoading = true;
    // this._spinner.showSpinner();
    let inputData = this._taDataStore.getInputData(this.operation);
    let input = inputData?.output;
    //checking if the input data is empty
    if (!input) {
      this.showTextError = true;
      // vm.showLoading = false;
      this._spinner.stopSpinner();
      return;
    }
    let fileName = fileDetails.fileName;
    let feature = fileDetails.featureName;
    let configData: any = {};
    if (fileName && feature) {
      configData.colName = feature;
      configData.fileName = fileName;
      configData.maxWords = wordFreqSliderVal;
      configData.projectId = this.project._id;
    } else {
      configData.data = input;
      configData.maxWords = wordFreqSliderVal;
    }
    let wordConfig = [
      {
        operationName: "wordFrequency",
        operationParameters: configData,
      },
    ];
    this.showOutput = true;
    this._textAnalysis
      .wordFrequency(configData)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this.output = res.data;
          let graphDiv: any = document.getElementById("wordFrequencyPlot");
          while (graphDiv.data && graphDiv.data.length > 0) {
            Plotly.deleteTraces(graphDiv, [0]);
          }
          this.output.layout.template.layout.title.xanchor = "center";
          (this.output.layout.template.layout.title.y = 0.9),
            (this.output.layout.template.layout.title.x = 0.5),
            (this.output.layout.template.layout.title.yanchor = "top");
          this.output.layout.autosize = true;
          //  var config = { responsive: true };
          Plotly.plot(graphDiv, this.output.data, this.output.layout);
          // store the data in the temporary service
          this._taDataStore.saveOperationOutput({
            uid: this.operation.uid,
            operation: this.operation.operation,
            output: this.output,
            taskConfig: wordConfig,
          });
          this._spinner.stopSpinner();
          // $rootScope.$broadcast("onSubmit");
          this._taDataStore.broadcastSubmitEvent("updateInputBar");
        },
        (err: Error) => {
          this.showOutput = false;
          this._spinner.stopSpinner();
          this._toaster.error("Server Error", "Error performing word frequency");
          // toaster.pop('error', $scope.app.name, 'Error occured at output generation', 3000);
        }
      );
    // TADataStore.saveOperationOutput({ uid: this.operation.uid, operation: 'wordFrequency', output: { dummydata: 'dummy wordFrequency data' } });
  };

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
